
import Vue from 'vue'
import Vuex from 'vuex'
import Bmob from 'hydrogen-js-sdk'

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {

        provinceList : ["", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    ""],

        countryList : ["", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", "", "", "", "", "", "",
    "", "", "", "", "", ""],


        hasLogin: false,
        curUser: '',
        curRole:'guest',
        curRealm:'None',

        reqlog:false,

        standard:['guest','visitor','prov_admin','super_admin'],
        links: [
          { icon: "fa-home", text: "", route: "/" ,need:'guest'},
          { icon: "fa-map-marker", text: "", route: "/Charts" ,need:'visitor'},
          { icon: "fa-cog", text: "", route: "/Dispatch" ,need:'prov_admin'},
          { icon: "fa-user-plus", text: "", route: "/Super" ,need:'super_admin'}
        ],

        dispatchOps:[
            {text:'',role:'super_admin',path:'/globalauto'},
            {text:'',role:'prov_admin',path:'/auto'},
        ],
        


    },
    mutations: {
        login(state,info){
            state.hasLogin=true
            // console.log(info)
            state.curUser = info.username
            state.curRole = info.role
            state.curRealm = info.realm
        },
        logout(state){
            state.hasLogin=false
            state.curUser = ''
            state.curRole = 'guest'
            state.curRealm = 'None'
        },
        dereq(state){
            state.reqlog = !state.reqlog
        },
        

    },
    actions: {},
    getters: {
        hasLogin(state){
            return state.hasLogin
        },
        reqlog(state){
            return state.reqlog
        },
        getFilteredLinks(state){
            return state.links.filter(v=>{
                return state.standard.indexOf(state.curRole)>=state.standard.indexOf(v.need)
   
            })
            // return state.links
        },
        getUserInfo(state){
            return {
                username:state.curUser,
                role:state.curRole,
                realm:state.curRealm
            }
        },

        getRoleGrade(state){
            return state.standard.indexOf(state.curRole)
        },

        // getter
        getFilteredDispatchOps(state){
            return state.dispatchOps.filter(v=>{
                return v.role==state.curRole
            })
        },

        getProvinces(state){
            return state.provinceList
        },
        getCountries(state){
            return state.countryList
        },
        getCities(state){
            var q = Bmob.Query('cities_stats')
            q.equalTo('provinceName','==',state.curRealm)
            q.find().then(res=>{
                var cities = []
                res.forEach(v=>{
                    cities.push(v.name)
                })
                console.log(cities)
                return cities
            })
        }

    },
        
})