        <v-tab :value="0"></v-tab>
        <v-tab :value="1"></v-tab>
        <v-tab :value="2"></v-tab>
        <v-tab :value="3"></v-tab>
        <v-tab :value="4"></v-tab>
        <v-tab :value="5">/</v-tab>
      </v-tabs>
      <v-spacer></v-spacer>

      <div class="title text-no-wrap mr-6"></div>
      <v-icon large light>fa-angle-double-left</v-icon>
    </v-toolbar>

    <div id="linegraph" :style="{width: '80%', height: '500px',margin:'auto'}"></div>
    <!-- <v-btn-toggle mandatory :value="0" @change="lineselectChange">
      <v-btn :value="0"></v-btn>

      <v-btn :value="1"></v-btn>

      <v-btn :value="2"></v-btn>

      <v-btn :value="3"></v-btn>

      <v-btn :value="4"></v-btn>

      <v-btn :value="5">/</v-btn>
    </v-btn-toggle>-->
    <v-divider class="mt-4"></v-divider>
    <v-toolbar elevation="0">
      <v-icon large light>fa-angle-double-right</v-icon>
      <v-toolbar-title class="ml-6"></v-toolbar-title>
    </v-toolbar>
    <v-data-table
