        <v-data-table :headers="worldHeader" :items="countryData" light :items-per-page="30"></v-data-table>
      </v-carousel-item>
      <v-carousel-item>
        <v-data-table :headers="USAHeader" :items="USAProvinceData" light :items-per-page="30"></v-data-table>
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

