<template>
  <v-container fluid>
    <v-dialog
      v-model="alert"
      persistent
      :overlay="false"
      max-width="300px"
      transition="dialog-transition"
    >
      <v-card>
        <v-card-title primary-title></v-card-title>
        <v-card-text>{{alertText}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="alert=false" class="mr-4"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout row wrap>
      <v-flex xs10 offset-xs1>
        <v-stepper v-model="e1">
          <v-stepper-header>
            <v-stepper-step :complete="e1 > 1" step="1"></v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step :complete="e1 > 2" step="2"></v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3"></v-stepper-step>
          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content step="1">
              <v-card class="mb-12" height="300px">
                <v-card-title class="headline">
                  <v-layout row wrap>
                    <v-flex xs2 class="mt-8 mb-3">
                      <v-switch label="" v-model="v1" class="ma-2"></v-switch>
                    </v-flex>
                    <v-flex xs8 offset-xs1 class="mt-8 mb-3">
                      <v-select
                        outlined
                        v-model="selectedProvince"
                        label=""
                        :items="getHeadAndProvinces"
                        prepend-icon="fa-info"
                        autofocus
                        :disabled="!v1"
                      ></v-select>
                    </v-flex>

                    <v-flex xs2>
                      <v-switch label="" v-model="v2" class="ma-2"></v-switch>
                    </v-flex>
                    <v-flex xs8 offset-xs1>
                      <v-select
                        outlined
                        v-model="selectedCountry"
                        label=""
                        :items="getHeadAndCountries"
                        prepend-icon="fa-info"
                        autofocus
                        :disabled="!v2"
                      ></v-select>
                    </v-flex>
                  </v-layout>
                </v-card-title>
              </v-card>
              <v-spacer></v-spacer>
              <v-btn color="primary" :disabled="!(v1||v2)" @click="e1 = 2" class="float-right"></v-btn>
            </v-stepper-content>

            <v-stepper-content step="2">
              <v-card class="mb-12" height="450px">
                <v-layout row wrap>
                  <v-flex xs4 >
                    <v-time-picker headed color="primary" v-model="time"></v-time-picker>
                  </v-flex>
                  <v-flex xs5 offset-xs1>
                    <v-alert
                      border="left"
                      colored-border
                      color="primary"
                      elevation="2"
                      hover
                      class="mb-12 mt-8"
                    >{{time}}</v-alert>
                    <v-chip class="mb-4">Tips: </v-chip>
                    <v-chip class="mb-4">Tips: </v-chip>
                    <v-chip class="mb-4">Tips: </v-chip>
                  </v-flex>
                </v-layout>
              </v-card>
              <div class="float-right">
                <v-btn color="primary" @click="e1 = 3"></v-btn>
                <v-btn text @click="e1 = 1"></v-btn>
              </div>
            </v-stepper-content>

            <v-stepper-content step="3">
              <v-card class="mb-12" height="20 0px">
                <v-layout row wrap>
                  <v-flex xs6 offset-xs3>
                    <v-alert
                      border="left"
                      colored-border
                      color="primary"
                      elevation="2"
                      hover
                      class="mb-12 mt-8"
                    >
                      <div></div>
                      <div>
                        <v-chip v-if="v1" class="ml-2 mb-3">{{selectedProvince}}</v-chip>
                        <v-chip v-if="v2" class="ml-2 mb-3">{{selectedCountry}}</v-chip>
                        <v-chip class="ml-2 mb-3">{{time}}</v-chip>
                      </div>
                    </v-alert>
                  </v-flex>
                </v-layout>
              </v-card>
              <div class="float-right">
                <v-btn color="primary" @click="autoCommit"></v-btn>
                <v-btn text @click="e1=2"></v-btn>
              </div>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { store } from "@/store/index";
import lib from "@/lib/funcs.js";
export default {
  computed: {
    getHeadAndProvinces() {
      var data = store.getters.getProvinces;
      data.unshift("");
      return data;
    },
    getHeadAndCountries() {
      var data = store.getters.getCountries;
      data.unshift("");
      return data;
    }
  },

  data() {
    return {
      e1: 1,
      selectedProvince: "",
      selectedCountry: "",
      v1: true,
      v2: true,
      time: "00:00",
      alert: false,
      alertText: "",
    };
  },

  methods: {


    autoCommit() {
      if (this.v1) {
        if (this.selectedProvince == "") {
          lib.multiTimesFuncs.updateCountryOrProvinceStats("province");
          lib.multiTimesFuncs.updateCountryOrProvinceDaily("province");
         
          // lib.multiTimesFuncs.updateCities('all')
        } else {
          var name = this.selectedProvince;
          lib.multiTimesFuncs.updateSpecific("province", name);
          // lib.multiTimesFuncs.updateCities(name)
        }
         lib.multiTimesFuncs.updateSpecific("country", '');
      }
      if (this.v2) {
        if (this.selectedCountry == "") {
          lib.multiTimesFuncs.updateCountryOrProvinceStats("country");
          lib.multiTimesFuncs.updateCountryOrProvinceDaily("country");
        } else {
          lib.multiTimesFuncs.updateSpecific("country", this.selectedCountry);
        }
      }
      this.showTips("")
    },
    showTips(text) {
      this.alertText = text;
      this.alert = true;
    }

  },


};
</script>
