<template>
  <div>
    <v-toolbar elevation="0" id="world_info" class>
      <v-icon large light>fa-angle-double-right</v-icon>

      <v-spacer></v-spacer>
      <v-tabs :value="0" class="ml-12 mr-12" @change="opChange">
        <v-tab class="float-right" :value="0"></v-tab>
        <v-tab class="float-right" :value="1"></v-tab>
      </v-tabs>
    </v-toolbar>
    <div v-show="whichOp==0">
      <v-layout row wrap>
        <v-flex xs10 offset-xs1>
          <v-data-table
            :headers="adminHeader"
            :items="provAdmins"
            :single-expand="true"
            :items-per-page="34"
            class="elevation-1 mb-12"
          ></v-data-table>
        </v-flex>
      </v-layout>
    </div>

    <div v-show="whichOp==1">
      <v-layout row wrap>
        <v-flex xs10 offset-xs1>
          <v-stepper v-model="e1">
            <v-stepper-header>
              <v-stepper-step :complete="e1 > 1" step="1"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 2" step="2"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step step="3"></v-stepper-step>
            </v-stepper-header>

            <v-stepper-items>
              <v-stepper-content step="1">
                <v-card class="mb-12" height="250px">
                  <v-card-title class="headline">
                    <v-layout row wrap>
                      <v-flex xs2 class="mt-8 mb-3">
                        <v-switch :label="getTips" v-model="v1" class="ma-2"></v-switch>
                      </v-flex>
                      <v-flex xs8 offset-xs1 class="mt-8 mb-3">
                        <v-select
                          outlined
                          :disabled="!v1"
                          v-model="adder"
                          label=""
                          :items="addOps"
                          prepend-icon="fa-info"
                          autofocus
                        ></v-select>
                      </v-flex>

                      <v-flex xs2></v-flex>
                      <v-flex xs8 offset-xs1>
                        <v-select
                          outlined
                          :disabled="v1"
                          v-model="deler"
                          label=""
                          :items="delOps"
                          prepend-icon="fa-info"
                          autofocus
                        ></v-select>
                      </v-flex>
                    </v-layout>
                  </v-card-title>
                </v-card>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="e1 = 2" class="float-right"></v-btn>
              </v-stepper-content>

              <v-stepper-content step="2">
                <v-card class="mb-12" height="250px">
                  <div v-show="v1&&adder==''">
                    <v-layout row wrap>
                      <v-flex xs5 offset-xs2 class="mt-4">
                        <v-select
                          outlined
                          v-model="chosenProv"
                          label=""
                          :items="getProvinces"
                          prepend-icon="fa-info"
                          autofocus
                        ></v-select>
                      </v-flex>

                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12">{{getCount}}</v-chip>
                      </v-flex>
                      <v-flex xs5 offset-xs2>
                        <v-text-field
                          v-model="adminName"
                          :counter="10"
                          :rules="[v => !!v || 'Not empty!']"
                          label=""
                          required
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12"></v-chip>
                      </v-flex>
                      <v-flex xs5 offset-xs2>
                        <v-text-field
                          v-model="password"
                          :counter="15"
                          :rules="[v => !!v || 'Not empty!']"
                          label=""
                          required
                          type="password"
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12">123456</v-chip>
                      </v-flex>
                    </v-layout>
                  </div>
                  <div v-show="v1&&adder==''">
                    <v-layout row wrap>
                      <v-flex xs5 offset-xs2>
                        <v-text-field
                          v-model="append"
                          :counter="8"
                          :rules="[v => !!v || 'Not empty!']"
                          label=""
                          required
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12">+</v-chip>
                      </v-flex>
                      <v-flex xs5 offset-xs2>
                        <v-text-field
                          v-model="password"
                          :counter="15"
                          :rules="[v => !!v || 'Not empty!']"
                          label=""
                          required
                          type="password"
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12">123456</v-chip>
                      </v-flex>
                    </v-layout>
                  </div>
                  <div v-show="!v1&&deler==''">
                    <v-layout row wrap>
                      <v-flex xs5 offset-xs2>
                        <v-text-field
                          v-model="delName"
                          :counter="10"
                          :rules="[v => !!v || 'Not empty!']"
                          label=""
                          required
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs5 class="mt-4">
                        <v-chip class="mt-3 ml-12"></v-chip>
                      </v-flex>
                    </v-layout>
                  </div>
                  <div v-show="!v1&&deler==''">
                    <v-layout row wrap>
                      <v-flex xs5 offset-xs2 class="mt-4">
                        <v-chip class="mt-3 ml-12"></v-chip>
                      </v-flex>
                    </v-layout>
                  </div>
                </v-card>
                <div class="float-right">
                  <v-btn color="primary" @click="()=>{e1 = 3;valid=true;}" :disabled="!valid"></v-btn>
                  <v-btn text @click="()=>{e1 = 1;valid=true}"></v-btn>
                </div>
              </v-stepper-content>

              <v-stepper-content step="3">
                <v-card class="mb-12" height="250px">
                  <v-layout row wrap>
                    <v-flex xs6 offset-xs3>
                      <v-alert
                        border="left"
                        colored-border
                        color="primary"
                        elevation="2"
                        hover
                        class="mb-12 mt-8"
                      >
                        <div></div>
                        <div>
                          <v-chip class="ml-2 mb-3">{{v1?adder:deler}}</v-chip>
                          <v-chip class="ml-2 mb-3">
                            
                            {{getConfig}}
                          </v-chip>
                        </div>
                      </v-alert>
                    </v-flex>
                  </v-layout>
                </v-card>
                <div class="float-right">
                  <v-btn color="primary" @click="submit"></v-btn>
                  <v-btn text @click="e1=2"></v-btn>
                </div>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
        </v-flex>
      </v-layout>
    </div>

    <v-dialog
      v-model="alert"
      persistent
      :overlay="false"
      max-width="300px"
      transition="dialog-transition"
    >
      <v-card>
        <v-card-title primary-title></v-card-title>
        <v-card-text>{{alertText}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="alert=false" class="mr-4"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>


<script>
import { store } from "../store/index";
import Bmob from "hydrogen-js-sdk";
export default {
  created() {
    this.setAdmins();
  },

  computed: {
    getRoleGrade() {
      return store.getters.getRoleGrade;
    },
    getConfig() {
      if (this.v1) {
        if (this.adder == "") {
          return (
            "" + this.chosenProv + "," + this.adminName
          );
        } else {
          return "" + this.append;
        }
      } else {
        if (this.deler == "") {
          return "" + this.delName;
        } else {
          return "";
        }
      }
    },
    getTips() {
      return this.v1 ? "" : "";
    },
    getCount() {
      if (this.chosenProv == "") return 0;
      var count = 0;
      this.provAdmins.forEach(v => {
        if (v.realm == this.chosenProv) count++;
      });
      return count;
    },

    getProvinces() {
      var data = store.getters.getProvinces;
      // data.unshift("");
      return data;
    },
    getHeadAndCountries() {
      var data = store.getters.getCountries;
      data.unshift("");
      return data;
    }
  },

  data() {
    return {
      whichOp: 0,
      isChange: false,
      provAdmins: [],
      adminHeader: [
        { text: "", value: "username" },
        { text: "", value: "realm" },
        { text: "", value: "createdAt" }
      ],

      e1: 1,
      selectedProvince: "",
      selectedCountry: "",
      v1: true,

      time: "00:00",
      alert: false,
      alertText: "",

      adder: "",
      deler: "",
      addOps: ["", ""],
      delOps: ["", ""],

      chosenProv: "",

      adminName: "",
      append: "",
      delName: "",
      password: "123456",

      valid: false
    };
  },

  methods: {
    test() {
      console.log(store);
      console.log(this.provAdmins);
    },
    setAdmins() {
      this.provAdmins = []
      var q = Bmob.Query("g2_user");
      q.equalTo("role", "==", "province_admin");
      q.find().then(res => {
        res.forEach(v => {
          this.provAdmins.push(v);
        });
        console.log(res);
        console.log(this.provAdmins);
      });
    },
    showTips(text) {
      this.alertText = text;
      this.alert = true;
    },

    opChange(v) {
      this.whichOp = v;
      if (this.isChange && v == 0) {
        this.setAdmins();
        this.isChange= false
      }
      console.log(v);
    },

    submit() {
      var q = Bmob.Query("g2_user");
      if (this.v1) {
        if (this.adder == "") {
          q.set("username", this.adminName);
          q.set("password", this.password);
          q.set("realm", this.chosenProv);
          q.set("role", "province_admin");
          q.save()
            .then(res => {
              console.log(res);
              this.showTips("");
              this.afterSubmit()
            })
            .catch(err => {
              console.log(err);
              this.showTips("");
            });
        } else {
          var q_arr = new Array();
          this.getProvinces.forEach(v => {
            var q2 = Bmob.Query("g2_user");
            q2.set("username", v + this.append);
            q2.set("password", this.password);
            q2.set("realm", v);
            q2.set("role", "province_admin");
            q_arr.push(q2);
          });

          Bmob.Query("g2_user")
            .saveAll(q_arr)
            .then(res => {
              console.log(res);
              this.showTips("");
              this.afterSubmit()
            })
            .catch(err => {
              console.log(err);
              this.showTips("");
            });
        }
      } else {
        if (this.deler == "") {
          q.equalTo("role", "==", "province_admin");
          q.equalTo('username','==',this.delName)
          q.find().then(res => {
            console.log(res)
            res
              .destroyAll()
              .then(r2 => {
                console.log(r2);
                this.showTips("");
                this.afterSubmit()
              })
              .catch(err => {
                console.log(err);
                this.showTips("");
              });
          });
        } else {
          q.limit(1000);
          q.equalTo("role", "==", "province_admin");
          q.find().then(res => {
            console.log(res)
            res
              .destroyAll()
              .then(r2 => {
                console.log(r2);
                this.showTips("");
                this.afterSubmit()
              })
              .catch(err => {
                console.log(err);
                this.showTips("");
              });
          });
        }
      }
      this.isChange = true
    },

    afterSubmit(){
      this.adder=""
      this.deler=''
      this.chosenProv=''
      this.adminName= "",
      this.append= "",
      this.delName= "",
      this.password= "123456",
      this.valid= false
      this.v1=true
      this.e1 = 1
    }
  },
  watch: {
       getRoleGrade(val, oldVal) {
      if (val < 3 || oldVal < 3) {
        // 
        this.$router.push({ path: "/" });
      }
    },
    adminName(val) {
      var flag = true;
      this.provAdmins.forEach(v => {
        if (v.username == val) {
          this.valid = false;
          flag = false;
        }
      });
      if (flag){
        if(val.length>10) this.valid = false;
        else this.valid = true;
      }
      
      
      
    },

    append(val) {
      var flag = true;
      this.provAdmins.forEach(v => {
        if (
          v.username.indexOf(val) != -1 &&
          v.username.length == val.length + 2
        ) {
          this.valid = false;
          flag = false;
        }
      });
      if (flag&&val.length<=8&&this.password.length<=15) this.valid = true;
    },

    delName(val) {
      var flag = true;
      this.provAdmins.forEach(v => {
        if (v.username == val) {
          this.valid = true;
          flag = false;
        }
      });
      if (flag||val.length>10) this.valid = false;
      if(this.delName.length>10) this.valid=false;
    },

    password(v){
      if(v.length>15) this.valid = false;
    }





  }
};
</script>